import { element } from 'protractor';
import { Post } from './models/post';
import { UserService } from './service/user.service';
import { GLOBAL } from './service/global';
import { User } from './models/user';
import { Component,OnInit } from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import { FacebookService, LoginResponse, LoginOptions, UIResponse, UIParams, FBVideoComponent } from 'ngx-facebook';
import { Categoria } from './models/categoria';
import { CategoriaService } from './service/categoria.service';
import { PostService } from './service/post.service';
import { Meta } from '@angular/platform-browser';
import {NgbModal, ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  providers:[UserService,CategoriaService,PostService]
})
export class AppComponent implements OnInit {
 
  title = 'jorgecastillodevelop.pro';
  public identity;
  public token;
  public posts:Post[];
  public user:User;
  public postcat:Post[];
  public cat:Categoria;
  public url:String;
  public errorMessage;
  public alertMessage;
  public on;
  public categoria:Categoria;
  public page=2;
  public closeResult: string;
  public search:String;  
 
  private countp;
  public cate;
  private nombrecat;
  private idcat;
  private contador=0;

  constructor(
    private _userService:UserService,
    private _route:ActivatedRoute,
    private _router:Router,
    private fb: FacebookService,
    private _categoriaService:CategoriaService,
    private _postService:PostService,
    private meta:Meta,
    private modalService: NgbModal

  )
  
  {
    this.user= new User('','','','','','','','');
    this.url=GLOBAL.url;
   
    

        this.meta.updateTag({ name: 'description', content: 'Hola somos codigo nica y en este blog en donde publicamos tutoriales, articulos y muchas cosas mas sobre tecnologia y mas' });
        this.meta.updateTag({ name: 'author', content: 'Jorge Castillo Moreno' });
        this.meta.updateTag({ name: 'keywords', content: 'Jorge Castillo, Informatico, Programador Nicaragua, Tecnologia Nicaragua' });
        this.meta.updateTag({property:'og:title', content:'Jorge Castillo JC Developer Nicaragua Articulos Tutoriales sobre tecnologias'});
        this.meta.updateTag({property:'og:type', content:'artículo'});
        this.meta.updateTag({property:'og:description', content:'JC Developer Nicaragua te ofrece los mejores servicios en el desarrollo de tu sitio web, aplicacion y lo que necesites en el desarrollo de tu negocio a la par de la tecnologia'});
        this.meta.updateTag({property:'og:url', content:'http://www.jorgecastillodeveloper.pro'});
        this.meta.updateTag({property:'og:image', content:'http://www.jorgecastillodeveloper.pro/assets/img/jorge.jpg'});

        this.meta.updateTag({property:'og:fb:admins', content:'https://www.facebook.com/jorgeacastillomoreno/'});
  }
  ngOnInit(): void {
      this.identity=this._userService.getIdentity();
      this.token=this._userService.getToken();
      this.getCategoria();
      
    //   console.log(this.identity);
    //   console.log(this.identity);
    }

    getCategoria(){
        
       
                
                   
                    this._categoriaService.getCategoria(this.token).subscribe(response=>{
                        if (!response.categoria) {
                            console.log('No Hay categoria');
                        } else {
                            this.cate=response.categoria;
                            
                           this._postService.getcountPost().subscribe(resp=>{
                               this.countp=resp.postcat;
                                for (let i = 0; i < this.countp.length; i++) {
                                    const element = this.countp[i];
                                        for (let j = 0; j < this.cate.length; j++) {
                                            const element2=this.cate[j];
                                           if (element.category==element2._id) {
                                               this.contador=this.contador+1;
                                               console.log(this.contador);
                                           }                                      
                                          
                                       }     
                                }
                           });
                            
                        }
                   
           
        },
        error=>{
                var errorMessage = <any>error;

                var body = JSON.parse(error._body);
                this.alertMessage = body.message;
                if (errorMessage !=null) { 
                this.alertMessage=body.message; 
                console.log(error);
                }
            });
    
    }

    // Metodo para contar los post por categoria

    

    SearchPost(search){
        //this._route.params.forEach((params:Params)=>{
            if(search==''){
                return this._router.navigate(['/']);
            }
            this.search=search;
           
            // let page=+params['page'];
            // if (!page) {
            //     page=2  
            // } else {
            //     this.page=page+2;

            // }
        

            this._postService.getsearchpost(this.search).subscribe(response=>{
                if (!response.searchPost) {
                    this._router.navigate(['/']);
                }else {
                    
                    this.posts=response.searchPost;
                    this._router.navigate(['/SearchPost/'+this.search]);
                }
            },error=>{
                var errorMessage = <any>error;
                
                var body = JSON.parse(error._body);
                this.alertMessage = body.message;
                if (errorMessage !=null) { 
                this.alertMessage=body.message; 
                console.log(error);
                }
            }
        
        );

        // });
        
        
    }



    

    onSubmit(){
      this._userService.singup(this.user).subscribe(response=>{
          let identity=response.user;
          this.identity=identity;
          //console.log(response.message);
          if(response.message){
            alert(response.message);
          }else if (!this.identity._id) {
              alert('El Usuario no esta correctamente logeado');
              console.log(this.identity);
          }else{
              //crear elemento del localStorage
              localStorage.setItem('identity',JSON.stringify(identity));
              //Conseguir el token para enviarlo a cada peticion
              this._userService.singup(this.user,'true').subscribe(response=>{
                  let token=response.token;
                  this.token=token;
                  if (this.token.length<=0) {
                      alert('El Token no se ha generado');
                   }else{
                       //se crea el elemento en el localStorage
                       localStorage.setItem('token',token);
                       this.user = new User('','','','','','','','');
                   }
                //    this._router.navigate(['/']);
              },error=>{
                  var errorMessage=<any>error;
                  var body=JSON.stringify(error._body);
                  if (errorMessage!=null) {
                      this.errorMessage=body;
                      console.log(error);
                  }
              }
          );
              
          }
      },error=>{
          var errorMessage=<any>error;
          var body=JSON.stringify(error._body);
          if (errorMessage!=null) {
              this.errorMessage=body;
              console.log(error);
              alert(this.errorMessage);
          }
      }
  );
  }

  logout(){
    localStorage.removeItem('identity');
    localStorage.removeItem('token');
    localStorage.clear();
    this.identity=null;
    this.token=null;
    this._router.navigate(['/']);
  }

  


  // Modal
  open(content) {
    this.modalService.open(content).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return  `with: ${reason}`;
    }
  }
// Fin


}



