import{Injectable} from '@angular/core';
import {Http, Response, Headers,RequestOptions} from '@angular/http';
import 'rxjs/add/operator/map';
import {Observable} from 'rxjs/Observable';
import  {GLOBAL} from './global';
import { Params } from '@angular/router';
import { User } from '../models/user';

    @Injectable()

    export class UserService{
        public url:String;
        public identity;
        public token;

        constructor(private _http:Http){
            this.url=GLOBAL.url;
        }

        singup(user_to_login,gethash=null){
            if (gethash!=null) {
                user_to_login.gethash=gethash;
            } 

            let json= JSON.stringify(user_to_login);
            let params=json;

            let headers=new Headers({'Content-Type':'application/json'});
            return this._http.post(this.url+'login',params,{headers:headers})
            .map(res=>res.json());
        }

        register(user_to_login){
            let json=JSON.stringify(user_to_login);
            let params=json;
            let headers= new Headers({
                'Content-Type':'application/json'
            });

            return this._http.post(this.url+'Register',params,{headers:headers})
                   .map(res=>res.json());
        }

        getUsers(token,page){
            let headers=new Headers({
                'Content-Type':'application/json',
                'Authorization':token
            });

            let options= new RequestOptions({headers:headers});
            return this._http.get(this.url+'users/'+page,options).map(res=>res.json());
        }

        getUserOne(token,id:String){
            let headers=new Headers({
                'Content-Type':'application/json',
                'Authorization':token
            });

            let options= new RequestOptions({headers:headers});
            return this._http.get(this.url+'userone/'+id,options).map(res=>res.json());
        }

        updateUser(token,user:User,id:String){
            let params = JSON.stringify(user);
            let headers=new Headers({
                'Content-Type':'application/json',
                'Authorization':token
            });

            let options= new RequestOptions({headers:headers});
            return this._http.put(this.url+'update-user/'+id,params,options).map(res=>res.json());
        }

        // Obtener roles de usuarios
        // getroles(){
        //     let headers=new Headers({
        //         'Content-Type':'application/json'
        //     });

        //     let options= new RequestOptions({headers:headers});
        //     return this._http.get(this.url+'getroles/',options).map(res=>res.json());
        // }
        // Fin


        //Obtener Token y Identity
        getIdentity(){
            let identity=JSON.parse(localStorage.getItem('identity'));
            if (identity!='undefined') {
                this.identity=identity;
            } else {
                this.identity=null;
            }
            return this.identity;
        }


        getToken(){
            let token= localStorage.getItem('token');
            if (token!='undefined') {
                this.token=token;
            } else {
                this.token=null;
            }
            return this.token;
        }
    }


